import PROJECTS from '../data/project';
import React from 'react';
import 'bootstrap/dist/css/bootstrap.min.css';

import './style/project_style.css';


const Project = props => {
  const {title, image, description, githubLink, demoLink} = props.project
  return (
    <div className="each-project col-md-3">
      <h3>{title}</h3>
      <div className="each-project--image-container">
        <img src={image} alt='profile' /> 
      </div>
      <div className="each-project--description">
       {description}
      </div>
      <div className="each-project--links">
        <div className="each-project--links--github">
          <a href={githubLink}>Code</a>
        </div>
        <div className="each-project--links--demo">
          {title !== 'NY-locations' ? <a href={demoLink}>Demo</a>: ''}
        </div>
      </div>
    </div>
  )
}

const Projects = () => (
  <div className="project-section container">
    <div className="row justify-content-around">
      {
        PROJECTS.map(PROJECT => (
            <Project key={PROJECT.id} project={PROJECT} />
        ))
      }
    </div>
  </div>
)

export default Projects;