import React from 'react';
import ReactDOM from 'react-dom';
import App from './components/App';
import Header from './components/Header';
import Project from './components/Project';
import Photos from './components/Photos';
import createBrowserHistory from 'history/createBrowserHistory';
import { Router, Switch, Route } from 'react-router-dom';
// const baseUrl = process.env.PUBLIC_URL; // will be /hypercomp
// console.log(baseUrl)



// By doing this, you can have separate css files for each compoent
// This will prevent you from having one large css file
import './index.css';


ReactDOM.render(
  <Router history={createBrowserHistory()}>
    <Switch>
      <Route exact path='/website/' render={ () => <Header><App /></Header>} />
      <Route path='website/projects/' render={() => <Header><Project /></Header>} />

      </Switch>
    </Router>
  ,document.getElementById('root')
);


