import React, { Component } from 'react';
import About from './About'
import SocialProfiles from './SocialProfile';

import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faArrowDown } from '@fortawesome/free-solid-svg-icons'

const arrowIcon = <FontAwesomeIcon icon={faArrowDown} />
const FADE_TIME = 1000;

class App extends Component {

  state = { fadeIn: true };
  componentDidMount() {
    setTimeout(() => this.setState({ fadeIn: false }), FADE_TIME);
    this.changeFadeInToTrue();
  }

  componentWillUnmount() {
    clearInterval(this.arrowInterval);
  }

  changeFadeInToTrue() {
    this.arrowInterval = setInterval(() => {
      this.setState({ fadeIn: true })
      setTimeout(() => this.setState({ fadeIn: false }), FADE_TIME);
    }, FADE_TIME * 2);
  }

  render() {
    const { fadeIn } = this.state;
    return (
      <div>
        <h1>
        </h1>
        <div className="intro">
          <div className="intro__section">
            <div className="intro__header">
              Hello, world!
            </div>
            <div className="intro__name">
              I'm Sherman
            </div>
            <div className="divider">
              <hr />
            </div>
            <SocialProfiles />
          </div>
          <div className={fadeIn ? 'arrow title__fade__in' : 'arrow title__fade__out'} >
            <a href="#about__section">
            {arrowIcon}
            </a>
          </div>
        </div>
        <About />
      </div>
    )
  }
}

export default App;
